.priority-overview {
  display: flex;
  flex-direction: row;
  gap: 0.625rem;
  justify-content: space-between;
  width: 100%;
  height: auto;
}

.priority-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #f8f9fa;
  padding: 0.9375rem;
  border-radius: 0.5rem;
  box-shadow: 0 0.25rem 0.375rem rgba(0, 0, 0, 0.1);
  width: 23%;
  height: auto;
  font-family: Arial, sans-serif;
}

.priority-item .vertical-line {
  width: 0.3125rem;
  height: 1.875rem;
  margin-right: 0.625rem;
  border-radius: 0.125rem;
}

.priority-item .priority-info p {
  font-size: 0.75rem;
  margin: 0;
  color: #6c757d;
}

.priority-item .priority-info h2 {
  font-size: 1rem;
  margin: 0;
  color: #333;
}

.vertical-line.purple {
  background-color: #8e44ad;
}

.vertical-line.teal {
  background-color: #1abc9c;
}

.vertical-line.orange {
  background-color: #f39c12;
}

.vertical-line.green {
  background-color: #27ae60;
}

